export default {
	data() {
		return {
			title: 'Hello',
			img: '../../static/six.png', //图片
			weibo: '../../static/weibo.png', //微博logo
			weixin: '../../static/weixin.png', //微信logo
			user: '',//用户名
			password: '',//密码
			look: false,//是否同意协议
		}
	},
	onLoad() {

	},
	methods: {
		goPhone() {
			uni.navigateTo({
				url: "../phone/phone"
			})
		},
		formSubmit(){
			// console.log("提交")
			let _this = this;
			if(this.look){
				console.log("勾选了",_this.user,_this.password)
				uni.request({
					url: 'http://ceshi3.dishait.cn/api/login',
					data: {
						username: _this.user,
						password: _this.password
					},
					method: 'POST',
					success(e) {
						console.log(e.data.data)
						if(e.data.data){
							_this.date = e.data.data
							// sessionStorage.setItem("use", e.data.data.token)
							uni.setStorage({
								key: 'use',
								data: e.data.data.token
							})
							uni.setStorage({
								key: 'user',
								data: e.data.data
							})
							uni.reLaunch({
								url: "../Index/index"
							})
							let str = uni.getStorage({
								key: 'use'
							})
						}
						
						
					},
					fail(e) {
						console.log("账号密码错误")
					}
				})
			}
		},
		goBack(){
			uni.navigateBack({
				delta:5
			})
		},
		userChange(e){
			this.user = e.detail.value;
			// console.log(this.user);
		},
		passwordChange(e){
			this.password = e.detail.value;
			// console.log(this.password);
		},
		chan(e){
			// console.log(e.detail.value[0])
			if(e.detail.value[0]){
				this.look = true;
			}else{
				this.look = false;
			}
		}
	}
}
